.review-component {
  padding: 20px;
}

.review-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 20px;
}

.review-header h3 {
  color: var(--primary-color);
}

.close-button {
  background: none;
  font-size: 24px;
  color: #777;
  padding: 0;
  display: flex;
  justify-content: center;
  align-items: center;
  width: 36px;
  height: 36px;
  border-radius: 50%;
}

.close-button:hover {
  background-color: #f0f0f0;
}

.review-form {
  background-color: #f9f9f9;
  padding: 15px;
  border-radius: var(--border-radius);
  margin-bottom: 20px;
}

.rating-input {
  margin-bottom: 15px;
}

.rating-input p {
  margin-bottom: 5px;
  font-weight: 600;
}

.star-rating {
  display: flex;
  gap: 5px;
}

.star-button {
  background: none;
  border: none;
  padding: 0;
  font-size: 24px;
  cursor: pointer;
}

.star-button.active {
  color: #ffc107;
}

.comment-input {
  margin-bottom: 15px;
}

.comment-input label {
  display: block;
  margin-bottom: 5px;
  font-weight: 600;
}

.comment-input textarea {
  width: 100%;
  padding: 10px;
  border: 1px solid #ddd;
  border-radius: var(--border-radius);
  resize: vertical;
}

.submit-review-button {
  background-color: var(--primary-color);
  color: white;
  padding: 10px 20px;
  font-weight: 600;
  width: 100%;
}

.submit-review-button:hover {
  background-color: var(--primary-dark);
}

.submit-review-button:disabled {
  background-color: #ccc;
  cursor: not-allowed;
}

.reviews-list {
  margin-top: 30px;
}

.reviews-list h4 {
  margin-bottom: 15px;
  color: #555;
}

.review-card {
  background-color: white;
  border: 1px solid #eee;
  border-radius: var(--border-radius);
  padding: 15px;
  margin-bottom: 15px;
}

.review-rating {
  margin-bottom: 10px;
}

.review-comment {
  margin-bottom: 10px;
  color: #555;
}

.review-date {
  font-size: 14px;
  color: #888;
}

.no-reviews {
  color: #888;
  font-style: italic;
}

.reviews-loading, .reviews-error {
  text-align: center;
  padding: 20px;
}

.reviews-error {
  color: var(--danger-color);
}

@media (max-width: 768px) {
  .review-component {
    padding: 15px;
  }
}
