.spot-form-section {
  background-color: var(--card-color);
  padding: 20px;
  border-radius: var(--border-radius);
  box-shadow: var(--box-shadow);
}

.spot-form-section h2 {
  margin-bottom: 20px;
  color: var(--primary-color);
}

.spot-form {
  display: grid;
  gap: 15px;
}

.form-group {
  display: flex;
  flex-direction: column;
  gap: 5px;
}

.form-group label {
  font-weight: 600;
}

.form-group input[type="text"],
.form-group select {
  padding: 10px;
  border: 1px solid #ddd;
  border-radius: var(--border-radius);
  font-size: 16px;
}

.checkbox-group {
  flex-direction: row;
  align-items: center;
}

.checkbox-label {
  display: flex;
  align-items: center;
  gap: 10px;
  cursor: pointer;
}

.form-actions {
  display: flex;
  gap: 10px;
  margin-top: 10px;
}

.submit-button {
  background-color: var(--primary-color);
  color: white;
  padding: 10px 20px;
  font-weight: 600;
}

.submit-button:hover {
  background-color: var(--primary-dark);
}

.cancel-button {
  background-color: #ddd;
  color: #333;
  padding: 10px 20px;
}

.cancel-button:hover {
  background-color: #ccc;
}

@media (max-width: 768px) {
  .spot-form-section {
    padding: 15px;
  }
  
  .form-actions {
    flex-direction: column;
  }
}
